form {
    width: 100%;
    display: flex;
}

form, fieldset {
    float: left;
}

fieldset {
    width: 33%;
    height: 100%;
    border: none;
    padding: 10px;
    margin: 5px;
}

.disabled {
    opacity: 0.5;
}

fieldset legend {
    background: white;
    font-weight: bold;
    padding: 5px 10px;
    font-size: 20px;
    border-radius: 10px;
}

h1 {
    color: white;
}

.step1 {
    background-color: #a4e0bc;
}

.step2 {
    background-color: #bcdeec;
}

.step3 {
    background-color: #e9cdb6;
}

body {
    background-image: linear-gradient(black, grey);
    color: black;
    font-family: Helvetica;
}

label {
    font-style: italic;
}

p {
    font-style: italic;
}

select {
    margin: 5px;
}

input {
    margin: 5px;
}

.label-textarea-wrap {
    width: 200px;
}

textarea {
    margin-top: 10px;
}

.icon-container {
    display: flex;
    width: 100%;
    justify-content: center;
}

.fa-check-circle {
    font-size: 80px;
    margin-top: 30px;
    color: #ccfbc0;
    background-color: green;
    border-radius: 50%;
}
